export interface UtilityCompany {
  id: string;
  name: string;
  country: string;
  region?: string;
  logoUrl?: string;
  portalUrl: string;
  quickAccessUrl?: string;
  apiEndpoint?: string;
  authType: 'oauth' | 'basic' | 'api_key' | 'custom';
  supportedFeatures: UtilityFeature[];
  status: 'active' | 'maintenance' | 'deprecated';
}

export interface UtilityFeature {
  type: 'billing' | 'usage' | 'outages' | 'payments' | 'smart_meter' | 'green_energy';
  available: boolean;
  description: string;
}

export interface UtilityCredentials {
  companyId: string;
  username: string;
  password?: string;
  apiKey?: string;
  accessToken?: string;
  refreshToken?: string;
  expiresAt?: Date;
}

export interface UtilityAccountData {
  accountNumber: string;
  customerName: string;
  serviceAddress: string;
  currentBalance: number;
  dueDate?: Date;
  lastBillAmount: number;
  currentUsage: {
    value: number;
    unit: 'kWh' | 'kW' | 'therms' | 'ccf';
    period: string;
  };
  billingHistory: BillingRecord[];
  usageHistory: UsageRecord[];
}

export interface BillingRecord {
  date: Date;
  amount: number;
  dueDate: Date;
  paid: boolean;
  usageKwh: number;
}

export interface UsageRecord {
  date: Date;
  usage: number;
  cost: number;
  peakHours?: number;
  offPeakHours?: number;
}

