// Utility integration functions
export async function searchUtilityCompanies(query: string, country?: string): Promise<UtilityCompany[]> {
  return new Promise((resolve) => {
    setTimeout(() => {
      let results = UTILITY_COMPANIES.filter(company => 
        company.name.toLowerCase().includes(query.toLowerCase()) ||
        company.region?.toLowerCase().includes(query.toLowerCase())
      );
      
      if (country) {
        results = results.filter(company => 
          company.country.toLowerCase() === country.toLowerCase()
        );
      }
      
      resolve(results);
    }, 300);
  });
}

export async function authenticateUtility(
  companyId: string, 
  credentials: Partial<UtilityCredentials>
): Promise<{ success: boolean; token?: string; error?: string }> {
  return new Promise((resolve) => {
    setTimeout(() => {
      // Simulate authentication process
      const company = UTILITY_COMPANIES.find(c => c.id === companyId);
      
      if (!company) {
        resolve({ success: false, error: 'Utility company not found' });
        return;
      }

      if (!credentials.username || !credentials.password) {
        resolve({ success: false, error: 'Username and password required' });
        return;
      }

      // Simulate 90% success rate
      if (Math.random() < 0.9) {
        resolve({ 
          success: true, 
          token: `auth_token_${companyId}_${Date.now()}` 
        });
      } else {
        resolve({ 
          success: false, 
          error: 'Invalid credentials or service temporarily unavailable' 
        });
      }
    }, 1500);
  });
}

export async function fetchUtilityData(
  companyId: string, 
  token: string
): Promise<UtilityAccountData> {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      const company = UTILITY_COMPANIES.find(c => c.id === companyId);
      
      if (!company) {
        reject(new Error('Utility company not found'));
        return;
      }

      // Generate realistic mock data
      const mockData: UtilityAccountData = {
        accountNumber: `${companyId.toUpperCase()}-${Math.random().toString().substr(2, 8)}`,
        customerName: 'John Doe',
        serviceAddress: '123 Main Street, City, State 12345',
        currentBalance: parseFloat((Math.random() * 200 + 50).toFixed(2)),
        dueDate: new Date(Date.now() + 15 * 24 * 60 * 60 * 1000), // 15 days from now
        lastBillAmount: parseFloat((Math.random() * 150 + 75).toFixed(2)),
        currentUsage: {
          value: parseFloat((Math.random() * 800 + 200).toFixed(1)),
          unit: 'kWh',
          period: 'This month'
        },
        billingHistory: generateBillingHistory(),
        usageHistory: generateUsageHistory()
      };

      resolve(mockData);
    }, 1000);
  });
}

function generateBillingHistory(): BillingRecord[] {
  const history: BillingRecord[] = [];
  const now = new Date();
  
  for (let i = 0; i < 12; i++) {
    const date = new Date(now.getFullYear(), now.getMonth() - i, 1);
    const dueDate = new Date(date.getFullYear(), date.getMonth(), 25);
    const amount = parseFloat((Math.random() * 100 + 80).toFixed(2));
    const usageKwh = Math.floor(Math.random() * 500 + 400);
    
    history.push({
      date,
      amount,
      dueDate,
      paid: i > 0, // Current month unpaid, others paid
      usageKwh
    });
  }
  
  return history;
}

function generateUsageHistory(): UsageRecord[] {
  const history: UsageRecord[] = [];
  const now = new Date();
  
  for (let i = 0; i < 30; i++) {
    const date = new Date(now.getTime() - i * 24 * 60 * 60 * 1000);
    const usage = parseFloat((Math.random() * 30 + 15).toFixed(1));
    const cost = parseFloat((usage * 0.12).toFixed(2));
    
    history.push({
      date,
      usage,
      cost,
      peakHours: parseFloat((usage * 0.6).toFixed(1)),
      offPeakHours: parseFloat((usage * 0.4).toFixed(1))
    });
  }
  
  return history;
}

export function getUtilityCompanyById(id: string): UtilityCompany | undefined {
  return UTILITY_COMPANIES.find(company => company.id === id);
}

export function getUtilityCompaniesByCountry(country: string): UtilityCompany[] {
  return UTILITY_COMPANIES.filter(company => 
    company.country.toLowerCase() === country.toLowerCase()
  );
}
