"use client";

import { useState, useEffect, useCallback } from "react";
import {
  UtilityCompany,
  UtilityCredentials,
  UtilityAccountData,
  searchUtilityCompanies,
  authenticateUtility,
  fetchUtilityData,
  getUtilityCompanyById
} from "@/lib/utilityIntegration";

export interface UseUtilityIntegrationOptions {
  autoLoad?: boolean;
  refreshInterval?: number;
}

export interface UseUtilityIntegrationReturn {
  // Company search
  companies: UtilityCompany[];
  searchCompanies: (query: string, country?: string) => Promise<void>;
  searchLoading: boolean;
  
  // Authentication
  authenticate: (companyId: string, credentials: Partial<UtilityCredentials>) => Promise<boolean>;
  isAuthenticated: boolean;
  authLoading: boolean;
  authError: string | null;
  
  // Account data
  accountData: UtilityAccountData | null;
  dataLoading: boolean;
  dataError: string | null;
  refreshData: () => Promise<void>;
  
  // Current utility
  currentUtility: UtilityCompany | null;
  
  // Connection management
  disconnect: () => void;
  clearErrors: () => void;
}

export function useUtilityIntegration(options: UseUtilityIntegrationOptions = {}): UseUtilityIntegrationReturn {
  const { autoLoad = false, refreshInterval = 300000 } = options; // 5 minutes default

  // Company search state
  const [companies, setCompanies] = useState<UtilityCompany[]>([]);
  const [searchLoading, setSearchLoading] = useState(false);

  // Authentication state
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [authLoading, setAuthLoading] = useState(false);
  const [authError, setAuthError] = useState<string | null>(null);
  const [authToken, setAuthToken] = useState<string | null>(null);
  const [currentUtilityId, setCurrentUtilityId] = useState<string | null>(null);

  // Account data state
  const [accountData, setAccountData] = useState<UtilityAccountData | null>(null);
  const [dataLoading, setDataLoading] = useState(false);
  const [dataError, setDataError] = useState<string | null>(null);

  // Derived state
  const currentUtility = currentUtilityId ? getUtilityCompanyById(currentUtilityId) || null : null;

  // Search utility companies
  const searchCompanies = useCallback(async (query: string, country?: string) => {
    if (!query.trim()) {
      setCompanies([]);
      return;
    }

    setSearchLoading(true);
    try {
      const results = await searchUtilityCompanies(query, country);
      setCompanies(results);
    } catch (error) {
      console.error('Error searching utility companies:', error);
      setCompanies([]);
    } finally {
      setSearchLoading(false);
    }
  }, []);

  // Authenticate with utility company
  const authenticate = useCallback(async (
    companyId: string, 
    credentials: Partial<UtilityCredentials>
  ): Promise<boolean> => {
    setAuthLoading(true);
    setAuthError(null);

    try {
      const result = await authenticateUtility(companyId, credentials);
      
      if (result.success && result.token) {
        setIsAuthenticated(true);
        setAuthToken(result.token);
        setCurrentUtilityId(companyId);
        
        // Store credentials securely (in a real app, use secure storage)
        localStorage.setItem('utility_auth', JSON.stringify({
          companyId,
          token: result.token,
          timestamp: Date.now()
        }));
        
        return true;
      } else {
        setAuthError(result.error || 'Authentication failed');
        return false;
      }
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Authentication error';
      setAuthError(errorMessage);
      return false;
    } finally {
      setAuthLoading(false);
    }
  }, []);

  // Fetch account data
  const refreshData = useCallback(async () => {
    if (!isAuthenticated || !authToken || !currentUtilityId) {
      return;
    }

    setDataLoading(true);
    setDataError(null);

    try {
      const data = await fetchUtilityData(currentUtilityId, authToken);
      setAccountData(data);
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Failed to fetch account data';
      setDataError(errorMessage);
      
      // If token is invalid, clear authentication
      if (errorMessage.includes('unauthorized') || errorMessage.includes('token')) {
        disconnect();
      }
    } finally {
      setDataLoading(false);
    }
  }, [isAuthenticated, authToken, currentUtilityId]);

  // Disconnect from utility
  const disconnect = useCallback(() => {
    setIsAuthenticated(false);
    setAuthToken(null);
    setCurrentUtilityId(null);
    setAccountData(null);
    setAuthError(null);
    setDataError(null);
    
    // Clear stored credentials
    localStorage.removeItem('utility_auth');
  }, []);

  // Clear errors
  const clearErrors = useCallback(() => {
    setAuthError(null);
    setDataError(null);
  }, []);

  // Auto-load stored authentication on mount
  useEffect(() => {
    if (autoLoad) {
      try {
        const stored = localStorage.getItem('utility_auth');
        if (stored) {
          const { companyId, token, timestamp } = JSON.parse(stored);
          
          // Check if token is not too old (24 hours)
          if (Date.now() - timestamp < 24 * 60 * 60 * 1000) {
            setIsAuthenticated(true);
            setAuthToken(token);
            setCurrentUtilityId(companyId);
          } else {
            // Token expired, clear it
            localStorage.removeItem('utility_auth');
          }
        }
      } catch (error) {
        console.error('Error loading stored authentication:', error);
        localStorage.removeItem('utility_auth');
      }
    }
  }, [autoLoad]);

  // Auto-refresh data
  useEffect(() => {
    if (isAuthenticated && accountData) {
      const interval = setInterval(refreshData, refreshInterval);
      return () => clearInterval(interval);
    }
  }, [isAuthenticated, accountData, refreshData, refreshInterval]);

  // Fetch initial data when authenticated
  useEffect(() => {
    if (isAuthenticated && !accountData && !dataLoading) {
      refreshData();
    }
  }, [isAuthenticated, accountData, dataLoading, refreshData]);

  return {
    // Company search
    companies,
    searchCompanies,
    searchLoading,
    
    // Authentication
    authenticate,
    isAuthenticated,
    authLoading,
    authError,
    
    // Account data
    accountData,
    dataLoading,
    dataError,
    refreshData,
    
    // Current utility
    currentUtility,
    
    // Connection management
    disconnect,
    clearErrors
  };
}
