"use client";

import React, { useState } from "react";
import { useUtilityIntegration } from "@/hooks/useUtilityIntegration";
import { UtilityCompany } from "@/lib/utilityIntegration";

export function MobileUtilityIntegration() {
  const {
    companies,
    searchCompanies,
    searchLoading,
    authenticate,
    isAuthenticated,
    authLoading,
    authError,
    accountData,
    dataLoading,
    dataError,
    refreshData,
    currentUtility,
    disconnect,
    clearErrors
  } = useUtilityIntegration({ autoLoad: true });

  const [searchQuery, setSearchQuery] = useState("");
  const [selectedCompany, setSelectedCompany] = useState<UtilityCompany | null>(null);
  const [credentials, setCredentials] = useState({ username: "", password: "" });
  const [showLoginForm, setShowLoginForm] = useState(false);

  const handleSearch = async (query: string) => {
    setSearchQuery(query);
    if (query.length > 2) {
      await searchCompanies(query);
    }
  };

  const handleCompanySelect = (company: UtilityCompany) => {
    setSelectedCompany(company);
    setShowLoginForm(true);
    clearErrors();
  };

  const handleLogin = async () => {
    if (!selectedCompany || !credentials.username || !credentials.password) {
      return;
    }

    const success = await authenticate(selectedCompany.id, credentials);
    if (success) {
      setShowLoginForm(false);
      setCredentials({ username: "", password: "" });
      setSelectedCompany(null);
      setSearchQuery("");
    }
  };

  const handleDisconnect = () => {
    disconnect();
    setShowLoginForm(false);
    setSelectedCompany(null);
    setCredentials({ username: "", password: "" });
  };

  if (isAuthenticated && currentUtility) {
    return (
      <div className="bg-white rounded-lg p-4 shadow-sm border border-gray-100">
        {/* Connected Header */}
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center">
            <div className="w-3 h-3 bg-green-500 rounded-full mr-2"></div>
            <div>
              <h2 className="text-lg font-semibold text-black">Connected to {currentUtility.name}</h2>
              <p className="text-xs text-gray-600">{currentUtility.country} • {currentUtility.region}</p>
            </div>
          </div>
          <button
            onClick={handleDisconnect}
            className="text-xs text-red-600 underline"
          >
            Disconnect
          </button>
        </div>

        {/* Account Data */}
        {dataLoading && (
          <div className="text-center py-4">
            <div className="inline-block animate-spin rounded-full h-6 w-6 border-b-2 border-black"></div>
            <p className="text-sm text-gray-500 mt-2">Loading account data...</p>
          </div>
        )}

        {dataError && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-3 mb-4">
            <p className="text-sm text-red-600 font-medium">Data Error</p>
            <p className="text-xs text-red-500 mt-1">{dataError}</p>
            <button
              onClick={refreshData}
              className="text-xs text-red-600 underline mt-2"
            >
              Retry
            </button>
          </div>
        )}

        {accountData && (
          <>
            {/* Account Summary */}
            <div className="mb-4 p-3 bg-gray-50 rounded-lg">
              <div className="flex justify-between items-center mb-2">
                <span className="text-sm text-gray-600">Account</span>
                <span className="text-sm font-medium text-black">{accountData.accountNumber}</span>
              </div>
              <div className="flex justify-between items-center mb-2">
                <span className="text-sm text-gray-600">Current Balance</span>
                <span className="text-lg font-bold text-black">${accountData.currentBalance.toFixed(2)}</span>
              </div>
              {accountData.dueDate && (
                <div className="flex justify-between items-center">
                  <span className="text-sm text-gray-600">Due Date</span>
                  <span className="text-sm text-black">{accountData.dueDate.toLocaleDateString()}</span>
                </div>
              )}
            </div>

            {/* Current Usage */}
            <div className="mb-4 p-3 bg-blue-50 rounded-lg">
              <h3 className="text-sm font-medium text-black mb-2">Current Usage</h3>
              <div className="flex justify-between items-center">
                <span className="text-2xl font-bold text-blue-800">
                  {accountData.currentUsage.value} {accountData.currentUsage.unit}
                </span>
                <span className="text-sm text-blue-600">{accountData.currentUsage.period}</span>
              </div>
            </div>

            {/* Quick Actions */}
            <div className="grid grid-cols-2 gap-2 mb-4">
              <button
                onClick={() => window.open(currentUtility.portalUrl, '_blank')}
                className="p-3 border border-gray-300 rounded-lg text-center hover:bg-gray-50"
              >
                <p className="text-sm font-medium text-black">View Portal</p>
                <p className="text-xs text-gray-600">Full website</p>
              </button>
              {currentUtility.quickAccessUrl && (
                <button
                  onClick={() => window.open(currentUtility.quickAccessUrl, '_blank')}
                  className="p-3 border border-gray-300 rounded-lg text-center hover:bg-gray-50"
                >
                  <p className="text-sm font-medium text-black">Quick Access</p>
                  <p className="text-xs text-gray-600">Fast login</p>
                </button>
              )}
            </div>

            {/* Recent Bills */}
            <div className="mb-4">
              <h3 className="text-sm font-medium text-black mb-2">Recent Bills</h3>
              <div className="space-y-2">
                {accountData.billingHistory.slice(0, 3).map((bill, index) => (
                  <div key={index} className="flex justify-between items-center p-2 bg-gray-50 rounded">
                    <div>
                      <p className="text-sm font-medium text-black">
                        {bill.date.toLocaleDateString('en-US', { month: 'short', year: 'numeric' })}
                      </p>
                      <p className="text-xs text-gray-600">{bill.usageKwh} kWh</p>
                    </div>
                    <div className="text-right">
                      <p className="text-sm font-bold text-black">${bill.amount.toFixed(2)}</p>
                      <p className={`text-xs ${bill.paid ? 'text-green-600' : 'text-orange-600'}`}>
                        {bill.paid ? 'Paid' : 'Due'}
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* Supported Features */}
            <div>
              <h3 className="text-sm font-medium text-black mb-2">Available Features</h3>
              <div className="grid grid-cols-2 gap-1">
                {currentUtility.supportedFeatures.filter(f => f.available).map((feature, index) => (
                  <div key={index} className="flex items-center p-2 bg-green-50 rounded">
                    <div className="w-2 h-2 bg-green-500 rounded-full mr-2"></div>
                    <span className="text-xs text-green-800 capitalize">
                      {feature.type.replace('_', ' ')}
                    </span>
                  </div>
                ))}
              </div>
            </div>
          </>
        )}
      </div>
    );
  }

  return (
    <div className="bg-white rounded-lg p-4 shadow-sm border border-gray-100">
      <h2 className="text-lg font-semibold mb-3 text-black">Utility Integration</h2>
      
      {!showLoginForm ? (
        <>
          {/* Search */}
          <div className="mb-4">
            <label className="block text-sm font-medium text-black mb-2">
              Search Your Utility Company
            </label>
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => handleSearch(e.target.value)}
              placeholder="Enter company name or location..."
              className="w-full p-3 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-black focus:border-transparent"
            />
          </div>

          {/* Search Results */}
          {searchLoading && (
            <div className="text-center py-4">
              <div className="inline-block animate-spin rounded-full h-4 w-4 border-b-2 border-black"></div>
              <p className="text-sm text-gray-500 mt-2">Searching...</p>
            </div>
          )}

          {companies.length > 0 && (
            <div className="space-y-2 mb-4">
              <h3 className="text-sm font-medium text-black">Select Your Utility:</h3>
              {companies.map((company) => (
                <button
                  key={company.id}
                  onClick={() => handleCompanySelect(company)}
                  className="w-full p-3 border border-gray-300 rounded-lg text-left hover:bg-gray-50 transition-colors"
                >
                  <div className="flex justify-between items-start">
                    <div>
                      <p className="text-sm font-medium text-black">{company.name}</p>
                      <p className="text-xs text-gray-600">
                        {company.country} {company.region && `• ${company.region}`}
                      </p>
                    </div>
                    <div className={`text-xs px-2 py-1 rounded ${
                      company.status === 'active' ? 'bg-green-100 text-green-700' : 'bg-yellow-100 text-yellow-700'
                    }`}>
                      {company.status}
                    </div>
                  </div>
                  <div className="mt-2 flex flex-wrap gap-1">
                    {company.supportedFeatures.filter(f => f.available).slice(0, 3).map((feature, idx) => (
                      <span key={idx} className="text-xs bg-blue-100 text-blue-700 px-2 py-1 rounded">
                        {feature.type.replace('_', ' ')}
                      </span>
                    ))}
                  </div>
                </button>
              ))}
            </div>
          )}

          {searchQuery.length > 2 && companies.length === 0 && !searchLoading && (
            <div className="text-center py-4">
              <p className="text-sm text-gray-500">No utility companies found</p>
              <p className="text-xs text-gray-400 mt-1">Try a different search term</p>
            </div>
          )}
        </>
      ) : (
        <>
          {/* Login Form */}
          <div className="mb-4">
            <div className="flex items-center justify-between mb-3">
              <h3 className="text-sm font-medium text-black">Login to {selectedCompany?.name}</h3>
              <button
                onClick={() => setShowLoginForm(false)}
                className="text-xs text-gray-500 underline"
              >
                Back
              </button>
            </div>
            
            <div className="space-y-3">
              <div>
                <label className="block text-xs font-medium text-gray-700 mb-1">
                  Username/Email
                </label>
                <input
                  type="text"
                  value={credentials.username}
                  onChange={(e) => setCredentials(prev => ({ ...prev, username: e.target.value }))}
                  className="w-full p-2 border border-gray-300 rounded text-sm focus:ring-2 focus:ring-black focus:border-transparent"
                  placeholder="Enter your username"
                />
              </div>
              
              <div>
                <label className="block text-xs font-medium text-gray-700 mb-1">
                  Password
                </label>
                <input
                  type="password"
                  value={credentials.password}
                  onChange={(e) => setCredentials(prev => ({ ...prev, password: e.target.value }))}
                  className="w-full p-2 border border-gray-300 rounded text-sm focus:ring-2 focus:ring-black focus:border-transparent"
                  placeholder="Enter your password"
                />
              </div>
            </div>
          </div>

          {authError && (
            <div className="bg-red-50 border border-red-200 rounded-lg p-3 mb-4">
              <p className="text-sm text-red-600 font-medium">Login Error</p>
              <p className="text-xs text-red-500 mt-1">{authError}</p>
            </div>
          )}

          <button
            onClick={handleLogin}
            disabled={authLoading || !credentials.username || !credentials.password}
            className="w-full bg-black text-white py-2 px-4 rounded-lg text-sm font-medium hover:bg-gray-800 transition-colors disabled:bg-gray-400 disabled:cursor-not-allowed"
          >
            {authLoading ? "Connecting..." : "Connect Account"}
          </button>

          <div className="mt-3 p-3 bg-blue-50 border border-blue-200 rounded-lg">
            <p className="text-xs text-blue-800 font-medium mb-1">Secure Connection</p>
            <p className="text-xs text-blue-700">
              Your credentials are encrypted and used only to fetch your energy data. 
              We never store your password.
            </p>
          </div>
        </>
      )}

      {/* Info Section */}
      <div className="mt-4 pt-3 border-t border-gray-100">
        <p className="text-xs text-gray-500">
          Connect to your utility company to view real-time billing, usage data, and manage your account directly from WattWise.
        </p>
      </div>
    </div>
  );
}
